import { ActivatedRoute } from '@angular/router';
import { Answer } from '@app/shared/models/answer.model';
import { AnswerStore, ProjectStore, QuestionStore, UserStore } from '@app/core/stores';
import { Component, OnInit } from '@angular/core';
import { Project } from '@app/shared/models/project.model';
import { Question } from '@app/shared/models/question.model';
import { User } from '@app/shared/models/user.model';
import * as _ from 'lodash';

@Component({
  selector: 'ffufm-feedback-dashboard-projects-by-team',
  templateUrl: './projects-by-team.component.html',
  styleUrls: ['./projects-by-team.component.css']
})
export class ProjectsByTeamComponent implements OnInit {

  showLegend = 'true';
  projects: Project[];
  projectsByUser: Project[];
  teamId: string;
  questions: Question[];
  noData: string;

  totalBillableDays: number = 0;
  view: string = 'questions';

  team: User;
  questionId: string;
  answers: Answer[];
  answersByUser: any[];
  projectsByQuestion: {};
  projectsByAnswer: any[];
  arrayOfProjectsByQuestion: any[];
  projectInfo: Project;
  selectedProject: Project;
  answersByProject: any[];

  constructor(private route: ActivatedRoute,
              private projectStore: ProjectStore,
              private answerStore: AnswerStore,
              private userStore: UserStore,
              private questionStore: QuestionStore) { }

  ngOnInit() {
    this.route.params.subscribe(params => {
      this.projectsByQuestion = [];
      this.teamId = params.teamId;
      this.questionId = params.questionId;
      this.getProjectsByQuestion();
      this.getTeam();
    });
  }

  loadProjectInfo() {
    this.projectStore.getProjects()
        .subscribe((projects: Project[]) => {
          projects.forEach(project => {
            if (project.id === this.selectedProject.id) {
              this.projectInfo = project;
            }
          });
        });
  }

  loadProjectsByAnswer(questionId: string) {
    this.questions.forEach((question) => {
      if (question.id === questionId) {
        this.noData = question.content + '<br/><br/>no answers available.<br/><br/>submit an answer <a href="http://google.com">here</a>';
      }
    });
    this.projectsByAnswer = [];
    this.arrayOfProjectsByQuestion.forEach((answer) => {
      if (answer.question_id === questionId) {
        this.questions.forEach((question) => {
          if (question.id === questionId) {
            this.noData = question.content;
          }
        });
        this.projectsByAnswer = answer.answer;
      }
    });
  }

  loadAnswersByProject() {
    this.projects.forEach((project) => {
      if (project.id === this.selectedProject.id) {
        this.noData = project.name + '<br/><br/>no answers available.<br/><br/>submit an answer <a href="http://google.com">here</a>';
      }
    });
    this.answersByProject = [];
    this.answers.forEach((answer) => {
      if (this.selectedProject.id == answer.project_id.toString()) {
        this.questions.forEach((question) => {
          if (question.id == answer.question_id.toString()) {
            this.noData = this.selectedProject.name;
            this.answersByProject.push({
              'name': question.content,
              'value': answer.content
            });
          }
        })
      }
    });
  }

  getProjectsByUser() {
    this.projectsByUser = [];
    this.projectStore.getProjects()
        .subscribe((projects: Project[]) => {
          this.projects = projects;
        });

    this.projects.forEach(project => {
      if (project.teamLeadId == this.teamId) {
        project['value'] = Number(project.billableDays);
        this.totalBillableDays += project['value'];
        this.projectsByUser.push(project);
      }
    });
  }

  getQuestions() {
    this.questions = [];
    this.questionStore.getQuestions()
        .subscribe((questions: Question[]) => {
          questions.forEach((question) => {
            if (question.role_id == 0 || question.role_id == 1) {
              this.questions.push(question);
            }
          });
        });
  }

  getProjectsByQuestion() {
    this.getAnswers();
    this.getQuestions();
    this.getProjectsByUser();
    this.answersByUser = [];
    this.answers.forEach((answer) => {
      this.projectsByUser.forEach((project) => {
        this.questions.forEach((question) => {
          if (question.id == answer.question_id.toString()) {
            if (answer.project_id.toString() == project.id) {
              this.answersByUser.push({
                name: project.name,
                value: Number(answer.content),
                question_id: answer.question_id,
                project_id: project.id,
                team_id: this.teamId
              });
            }
          }
        })
      });
    });

    this.projectsByQuestion = _.mapValues(_.groupBy(this.answersByUser, 'question_id'),
      clist => clist.map(answer => _.omit(answer, 'question_id')));

    this.arrayOfProjectsByQuestion = [];

    Object.entries(this.projectsByQuestion).forEach(([key, value]) => {
      this.arrayOfProjectsByQuestion.push({'question_id': key, 'answer': value});
    });
  }

  getAnswers() {
    this.answerStore.getAnswers()
        .subscribe((answers: Answer[]) => {
          this.answers = answers;
        });
  }

  getTeam() {
    this.userStore.getUsers()
        .subscribe((users: User[]) => {
          users.forEach((user: User) => {
            if (user.id === this.teamId) {
              this.team = user;
            }
          })
        })
  }

}



