import { RouterModule, Routes } from '@angular/router';
import { NgModule } from '@angular/core';
import { ProjectsByTeamComponent } from '@app/ffufm-feedback-dashboard-features/projects-by-team/projects-by-team.component';

const projectsByTeamRoutes: Routes = [
  {
    path: '',
    component: ProjectsByTeamComponent
  }
];

@NgModule({
  declarations: [],
  imports: [
    RouterModule.forChild(projectsByTeamRoutes)
  ],
  exports: [RouterModule]
})

export class ProjectsByTeamRouting {}


